.calendar-header {
  background-color: #f2f2f2;
  box-shadow: 0 2px 4px 0 #dadada;
}
.calendar-header .content {
  display: flex;
  flex-wrap: wrap;
  padding-top: 40px;
  padding-bottom: 16px;
  justify-content: space-between;
  align-items: flex-end;
}
.calendar-header .item {
  display: flex;
  flex-basis: 100%;
  margin-bottom: 16px;
  justify-content: flex-end;
}
.calendar-header .item:last-child {
  margin-bottom: 0;
}
@media (min-width: 769px) {
  .calendar-header .item {
    flex-basis: auto;
    margin-bottom: 0;
  }
  .calendar-header .item:first-child {
    flex-basis: 400px;
    order: 2;
  }
}
.calendar-navigation .content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 16px;
  padding-bottom: 16px;
}
.calendar-navigation .title {
  margin: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  padding: 0 8px;
  text-transform: capitalize;
  min-width: 150px;
  text-align: center;
}
.calendar-navigation .switcher {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  flex-basis: 100%;
}
@media (min-width: 520px) {
  .calendar-navigation .switcher {
    flex-basis: auto;
    margin-bottom: 0;
    margin-right: 16px;
  }
}
.calendar-navigation .nav-btn {
  appearance: none;
  -webkit-appearance: none;
  background: none;
  border: 1px solid #dadada;
  height: 24px;
  width: 24px;
  font-size: 10px;
}
.calendar-navigation .nav-btn--today {
  width: auto;
  font-size: 14px;
  line-height: 100%;
  padding: 0 8px;
  margin: 0 auto;
}
@media (min-width: 520px) {
  .calendar-navigation .nav-btn--today {
    width: auto;
    font-size: 14px;
    line-height: 100%;
    padding: 0 8px;
    margin: 0 0;
  }
}
.handle-btn {
  appearance: none;
  -webkit-appearance: none;
  background: none;
  border: none;
  box-shadow: none;
  background-color: #0080ff;
  color: #ffffff;
  padding: 4px 8px;
  margin-right: 8px;
  border-radius: 2px;
  font-size: 14px;
  box-shadow: 0 2px 2px #c0c0c0;
  height: 32px;
  display: inline-flex;
  align-items: center;
}
.handle-btn:last-child {
  margin-right: 0;
}
.form-search {
  width: 100%;
  font-size: 14px;
}
.form-search .field {
  display: flex;
  align-items: center;
}
.form-search .field-label {
  display: block;
  margin-right: 8px;
}
.form-search input {
  appearance: none;
  -webkit-appearance: none;
  border: 0;
  height: 32px;
  width: 100%;
  padding: 4px 8px;
  box-shadow: inset 1px 2px 2px #c0c0c0, 1px 1px 2px #c0c0c0;
  border-radius: 2px;
}
.calendar {
  padding-bottom: 32px;
}
.calendar .month {
  border-right: none;
  border-bottom: none;
  display: flex;
  flex-wrap: wrap;
}
.calendar .month .month__day {
  display: flex;
  flex-direction: column;
  flex-basis: 14.2857143%;
  border-left: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
  min-height: 100px;
}
.calendar .month .month__day:nth-child(7n+7) {
  border-right: 1px solid #dadada;
}
.calendar .month .month__day:nth-child(-n+7) {
  border-top: 1px solid #dadada;
}
.calendar .month .assigned {
  background-color: #71d0ff;
}
.calendar .month .today {
  background-color: #dadada;
}
.calendar .week {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}
.calendar .week .week-day {
  padding: 8px;
  flex-basis: 14.2857143%;
  border-left: 1px solid #dadada;
  border-top: 1px solid #dadada;
  font-size: 14px;
}
.calendar .week .week-day:last-child {
  border-right: 1px solid #dadada;
}
@media (min-width: 769px) {
  .calendar .week {
    display: none;
  }
}
.day .day__header {
  display: flex;
  margin: 0;
  padding: 8px;
  font-weight: 400;
  font-size: 14px;
}
.day .day__header .day__descr {
  display: block;
  margin-right: 16px;
}
.day .day__header .day__descr:last-child {
  margin-right: 0;
}
.day .day__header .day-of-week {
  display: none;
}
@media (min-width: 769px) {
  .day .day__header .day-of-week {
    display: block;
  }
}
.day .day__events {
  display: none;
}
@media (min-width: 769px) {
  .day .day__events {
    display: block;
    padding: 8px;
  }
}
html {
  box-sizing: border-box;
  font-family: 'Roboto', Arial, sans-serif;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
.wrapper {
  max-width: 1600px;
  padding-left: 8px;
  padding-right: 8px;
  margin: 0 auto;
}
.col {
  padding-left: 8px;
  padding-right: 8px;
}
.col-1 {
  width: 100%;
}
.row {
  display: flex;
  margin-left: -8px;
  margin-right: -8px;
}
.row--wrap {
  flex-wrap: wrap;
}
